<template>
  <div>
    <div class="catalog-section-header">
      <div class="back-arrow">
        <router-link to="/catalog">
          <img src="/icons/left-arrow.svg"/>
        </router-link>
      </div>
      <div class="heading">
        {{ props.section }}
      </div>
      <div class="magnifier">
        <router-link to="/catalog_search">
          <img src="/icons/magnifier.svg"/>
        </router-link>
      </div>
    </div>
    <div class="under-header">

      <div class="settings-icon-container">
        <router-link :to="settings_link">
          <img src="/icons/settings.svg"/>
        </router-link>
      </div>
      <div v-for="(item, i) in searchItems" :key="i" style="margin: 2px;">
        <button
            class="app-blue-btn"
            v-if="item.value===true"
            @click="()=>changeSearchItem(item.name)">
          {{ item.text }} &nbsp;
          <img src="/icons/close_circled.svg" />
        </button>
        <button
            class="app-white-btn"
            v-else
            @click="()=>changeSearchItem(item.name)">
          {{ item.text }}
        </button>
      </div>
    </div>

  </div>
</template>

<script setup>
import {defineProps, reactive, ref} from "vue";
import store from "@/store";

const props = defineProps(['section'])

store.commit('setPreviousPageData',
    '/catalog_section/'+props.section, undefined)

const settings_link = ref('/catalog_search_filters/' + props.section)


const searchItems = reactive([
  { id: 1, text: 'Диван', name: 'sofa', value: true},
  { id: 12, text: 'Угловые диваны', name: 'triangle_sofa', value: false},
  { id: 11, text: 'Кресла', name: 'chairs', value: false},
])

const changeSearchItem = (itemName) => {
  for (let i = 0; i < searchItems.length; i++) {
    if (searchItems[i].name === itemName) {
      if (searchItems[i].value) {
        searchItems[i].value = false
      } else {
        searchItems[i].value = true
      }
      break;
    }
  }
}

</script>

<style src="../../assets/styles/buttons.css"></style>
<style scoped>

.settings-icon-container {
  margin: auto 5px;
}

.under-header {
  display: flex;

}

.magnifier {
  margin-right: 20px;
  margin-top: 10px;
}

.back-arrow {
  margin-left: 20px;
  margin-top: 10px;
}

.heading {
  font-family: Open Sans, serif;
  font-size: 17px;
  font-weight: 700;
  line-height: 20px;
  letter-spacing: 0em;
  text-align: center;

  margin: auto;
}

.catalog-section-header {
  height: 44px;
  display: flex;
  padding-top: 10px;
}

</style>